
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background: #ece9e6; /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #ece9e6, #ffffff); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(left, #ece9e6, #ffffff);
  background: linear-gradient(to right, #ece9e6, #ffffff); 
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


@font-face {
    font-family: 'Roboto Slab Light Italic';
    font-style: normal;
    font-weight: normal;
    src: local('Roboto Slab Light Italic'), url(/search-and-filter-with-react/static/media/RobotoSlab-LightItalic.c793d78d.woff) format('woff');
}

h2 {
    font-family: 'Roboto Slab Light Italic';
    /* font-size: 2rem; */
}

.card{
    box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.4);
    max-width: 200px;
    height: 300px;
    -webkit-transition: all 1s; /* Safari */
    transition: all 1s;
}

.card:hover,
.card:focus  {
	-webkit-transform: translatex(0px) translatey(-8px);
          transform:  translatex(0px) translatey(-8px);
}


.search-wrapper {
    margin-left: auto;
    margin-right: auto;
    display: block;
    background: #fff;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 10px;
    box-shadow: 0 0 5px rgba(10, 10, 10, 0.4);
}

input[type="search"] {
    font-size: .8em;
}

.description {
    text-align: center;
    font-size: 0.8em;
    color: #bbb;
    text-shadow: 0 2px 1px #fff;
}
